import React, { useState } from "react";

//search bar component with initial state

const Search = ({ searchUsers }) => {
  const [text, setText] = useState("");

  const onChange = (e) => setText(e.target.value);

  const onSubmit = (e) => {
    e.preventDefault();

    if (text === "") {
      window.alert("Enter a text");
    } else {
      //pass the query via callback function searchUser
      searchUsers(text);

      setText("");
    }
  };

  return (
    <form onSubmit={onSubmit} className="d-flex justify-content-center">
      <div className="form-inline my-4">
        <div className="form-group mr-2">
          <input
            type="text"
            name="text"
            className="form-control"
            value={text}
            onChange={onChange}
          />
        </div>
        <div className="form-group">
          <button className="btn btn-secondary">Search</button>
        </div>
      </div>
    </form>
  );
};

export default Search;
