import React, { useEffect } from "react";

const UserCard = ({ user, getUser, match }) => {
  useEffect(() => {
    getUser(match.params.login);
  }, []);
  const {
    login,
    id,
    node_id,
    avatar_url,
    html_url,
    name,
    company,
    blog,
    location,
    hireable,
    bio,
    public_repos,
    public_gists,
    followers,
    following,
  } = user;
  return (
    <div className="d-flex justify-content-center p-5">
      <div className="px-5">
        <img
          src={avatar_url}
          alt="avatar"
          style={{ width: "200px", height: "200px" }}
        ></img>
        <h5 className="card-title my-3">{login}</h5>
      </div>
      <div className="text-left">
        <p className="card-text">Name : {name}</p>
        <p className="card-text">Company : {company}</p>
        <p className="card-text">Blog : {blog}</p>
        <p className="card-text">Location : {location}</p>
        <p className="card-text">Repositories : {public_repos}</p>
        <p className="card-text">Gists : {public_gists}</p>
        <p className="card-text">Followers : {followers}</p>
        <p className="card-text">Following : {following}</p>

        <a href={html_url} className="btn btn-success">
          Go to Github
        </a>
      </div>
    </div>
  );
};

export default UserCard;
