import "./App.css";
import React, { Fragment, useState, useEffect } from "react";
import Navbar from "./components/Navbar";
import Users from "./components/Users";
import Search from "./components/Search";
import About from "./components/About";
import axios from "axios";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import UserCard from "./components/UserCard";

//main component with initial state

const App = ({ searchUsers, getUser }) => {
  const [users, setUsers] = useState([]);
  const [user, setUser] = useState({});
  const [loading, setLoading] = useState(false);

  // loading initial data using github API
  // when component mounts

  useEffect(async () => {
    //set loading to true untill get response
    setLoading(true);

    //fetch data using Axios
    const res = await axios.get(
      `https://api.github.com/users?client_id=${process.env.REACT_APP_CLIENT_ID}&client_secret=${process.env.REACT_APP_CLIENT_SECRET}`
    );

    // set loading to false
    setUsers(res.data);
    setLoading(false);
  }, []);

  // function for searching users

  searchUsers = async (text) => {
    //set loading to true untill get response
    setLoading(true);

    const res = await axios.get(
      `https://api.github.com/search/users?q=${text}&client_id=${process.env.REACT_APP_CLIENT_ID}&client_secret=${process.env.REACT_APP_CLIENT_SECRET}`
    );

    //set loading to false and display search results
    setUsers(res.data.items);
    setLoading(false);
  };

  getUser = async (username) => {
    setLoading(true);

    const res = await axios.get(
      `https://api.github.com/users/${username}?client_id=${process.env.REACT_APP_CLIENT_ID}&client_secret=${process.env.REACT_APP_CLIENT_SECRET}`
    );

    //set loading to false and display search results
    setUser(res.data);
    setLoading(false);
  };

  return (
    <Router>
      <div className="App">
        <Navbar />
        <Switch>
          <Route
            exact
            path="/"
            render={(props) => (
              <Fragment>
                <Search searchUsers={searchUsers} />
                <div className="container">
                  <Users loading={loading} users={users} />
                </div>
              </Fragment>
            )}
          />
          <Route path="/about" component={About} />
          <Route
            exact
            path="/users/:login"
            render={(props) => (
              <UserCard
                {...props}
                getUser={getUser}
                user={user}
                loading={loading}
              />
            )}
          />
        </Switch>
      </div>
    </Router>
  );
};

export default App;
